import { getCustomPageInfo } from '@/api/user'
import { xDeCode } from '@/utils/index'
export default {
  data () {
    return {
      htmlData: '',
    }
  },
  created () {
    getCustomPageInfo({id: xDeCode(this.$route.query.id)})
      .then(res => {
        this.$vux.loading.hide()
        if (res.data.errorcode === 0) {
          // 处理数据
          // console.log(res.data.data.content)
          this.htmlData = res.data.data.content
        } else {
          this.$vux.toast.text(res.data.errormsg, 'middle')
        }
      })
      .catch(err => {
        this.$vux.loading.hide()
        console.log(err)
        this.$vux.toast.text('网络异常！', 'middle')
      })
  },
  components: {
  },
  methods: {

  }
}



